*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}

.main{
    padding-top: 50px;
}

nav{
    height: 50px;
    width: 100%;
    background-color: gray;
    position: fixed;
    z-index: 50;
    display: flex;
    align-items: center;
    justify-content:space-around;    
}

.burger-btn{
    width: 30px;
    height: 30px;
    margin-left: 20px;
    position: relative;
    cursor: pointer;
}

.burger-btn::after{
    content: "";
    position: absolute;
    bottom: 0;
    width: 20px;
    background-color: black;
    height: 2px;

}
.burger-btn::before{
    content: "";
    position: absolute;
    top: 0;
    width: 20px;
    background-color: black;
    height: 2px;
}

.burger-btn span{
    position: absolute;
    margin-top: 4px;
    top: 9px;
    width: 20px;
    background-color: black;
    height: 2px;
}

.display_main{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.display_main a{
  text-decoration: none;
}

.header1{
    margin-top: 1rem;
    padding-top: 0.7rem;
    padding-bottom: 0.7rem;
    padding-left: 4rem;
    padding-right: 4rem;
    border: 1px solid blue;
    border-radius: 1rem;
    background-color: #5568AC;
    color: white;
    align-self: center;
}
.header2{
    margin: 1rem;
    padding-top: 0.7rem;
    padding-bottom: 0.7rem;
    padding-left: 1rem;
    padding-right: 1rem;
    border: 1px solid blue;
    border-radius: 1rem;
    background-color: #F8F8F8;
    color: black;
    align-self: center;
    text-align: center;
}

.header2 h4{
    margin-bottom: 5px;
}

.display_main ul{
    align-self: center;
    color: #384c8f;
}
.display_main ul li{
    margin: 3px;
    cursor: pointer;
}

.gallery img{
    height: 170px;
    width: 350px;
    border-radius: 1rem;
}

.gallery1,.gallery,.gallery3{
    align-self: center;
}

.gallery1 img{
    margin-top: 1rem;
    border-radius: 1rem;
    cursor: pointer;
}

.gallery3 img{
    margin-top: 1rem;
    height: 170px;
    width: 320px;
    border-radius: 1rem;
}

.card{
    border: 1px solid red;
    border-radius: 1rem;
    margin: 1px;
    margin-top: 1rem;
    padding: 2px;
}
.card1{
    border: 1px solid rgb(47, 55, 131);
    border-radius: 1rem;
    margin: 10px;
    margin-top: 1rem;
    padding: 3px;
    justify-items: center;
    text-align: center;
    box-shadow: 4px 2px 2px blue;
    
}
.card1 img{
    border-radius: 1rem;
}
.card2{
  border: 1px solid rgb(179, 63, 43);
  border-radius: 1rem;
  margin: 10px;
  margin-top: 1rem;
  padding: 1.5rem;
  justify-items: center;
  text-align: center;
  box-shadow: 4px 2px 2px rgb(145, 115, 51);
  
}
.card3{
  border: 1px solid rgb(119, 111, 37);
  border-radius: 1rem;
  margin: 10px;
  margin-top: 1rem;
  padding: 1.5rem;
  justify-items: center;
  text-align: center;
  box-shadow: 4px 2px 2px rgb(122, 111, 49);
  
}
.table{
    
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: repeat(2,1fr);
    justify-items: center;
    text-align: center;
}

footer{
    background-color: lightblue;
    width: 90%;
    margin: auto;
    margin-bottom: 1rem;
    padding: 1rem;
    text-align: center;
    border-radius: 1rem;
    margin-top: 1rem;
}

footer h1{
    margin-bottom: 1rem;
    color: red;
}

footer p{
    margin-bottom: 10px;
}

.card5 {
    align-self: center;
    background: #666666;
    width: 85%;
    padding: 30px 90px 90px 90px;
    border: 6px solid rgba(0, 0, 0, 0.3);
    box-shadow: 20px 20px 0 rgba(0, 0, 0, 0.3);
    border-radius: 50px;
    position: relative;
    margin: 1rem;
  }
  
  .card5 h2 {
    color: rgba(0, 0, 0, 0.3);
    font-size: 60px;
    text-transform: uppercase;
  }
  
  .card5 .row {
    position: relative;
    width: 100%;
    display: grid;
    grid: auto / auto auto;
    grid-template-columns: repeat(auto-fit,minmax(40%, 1fr));
    grid-gap: 30px;
  }
  
  .card5 .row .col {
    position: relative;
    width: 100%;
    margin: 30px 20px 40px 0;
    transition: 0.5s;
  }
  
  .card5 .row .form-group {
    position: relative;
    width: 100%;
    height: 40px;
    color: #ffffff;
  }
  
  .card5 .row .form-group input,
  .card5 .row .form-group textarea {
    position: absolute;
    width: 90%;
    height: 100%;
    background: transparent;
    outline: none;
    font-size: 24px;
    padding: 10px 0 10px 20px;
    border: 5px solid rgba(0, 0, 0, 0.3);
    box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.3);
    color: #ffffff;
    border-radius: 50px;
  }
  
  .card5 .row .form-group label {
    line-height: 40px;
    color: #ffffff;
    font-size: 24px;
    margin: 0 0 0 30px;
    display: block;
    pointer-events: none;
  }
  
  .row5 .col:nth-child(6) {
    margin-top: 64px;
  }
  
  .card5 .row .form-group input:focus,
  .card5 .row .form-group textarea:focus {
    border: 5px solid #ffffff;
    transition: all 0.5s;
  }
  
  .card5 .row input[type="submit"] {
    border: 5px solid rgba(0, 0, 0, 0.3);
    box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.3);
    padding: 10px;
    height: 100%;
    width: 50%;
    cursor: pointer;
    outline: none;
    background: transparent;
    text-transform: uppercase;
    color: #ffffff;
    line-height: 40px;
    font-size: 24px;
    font-weight: 700;
    border-radius: 45px;
    transition: all 0.4s;
  }
  
  .card5 .row input[type="submit"]:hover {
    border: 5px solid rgba(255, 255, 255, 1);
    color: #ffffff;
    transition: all 0.4s;
  }
  
  @media screen and (max-width: 900px) {
    .card5 .row {
      grid-template-columns: repeat(auto-fit,minmax(70%, 1fr));
    }
    
    .card5 {
      padding: 20px;
    }
    
    .card5 h2 {
      font-size: 34px;
    }
    
    .card5 .row input[type="submit"] {
      width: 100%;
    }
  }