.menu{
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 50px;
    left: 0;
    transform: translateX(-130%);
    transition: 0.2s;
}

.menu.active{
    transform: translateX(0);
}

/* .blur{
    width: 100vw;
    height: 100vh;
    backdrop-filter: blur(2px);
    position: absolute;
    left: 30%;
} */

.menu__content{
    width: 30%;
    height: 100%;
    background-color: lightgray;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: 0.3s;
}

.menu__header{
    font-size: 2rem;
    margin: 1rem;
    color:black ;
    border-bottom: 1px solid black;
    width: 100%;
    text-align: center;
}

ul li a{
    color: black;
    text-decoration: none;
}

ul li{
 display: flex;
 align-items: center;
 margin:1rem ;
}

ul{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.material-icons{
    margin-left:10px ;
}

@media screen and (max-width:768px){
    .menu__content{
        width: 100%;
    }
    .blur{
        backdrop-filter: none;
    }
}